html {
  scroll-behavior: smooth;
}

body {
  padding-top: 50px;
  background-image: url(https://images.pexels.com/photos/785429/pexels-photo-785429.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);
  background-size: cover;
  background-attachment: fixed;
}

.App {
  text-align: center;
}

.header {
  margin: auto;
  /* max-width: 1000px; */
}

.topMenu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  z-index: 1;

  background: rgb(27 27 27 / 85%);
  border-radius: 2px 2px 30px 30px;

  height: 50px;
  width: 1000px;
  margin: auto;
}

.topMenu ul {
  margin: 0px;
}

.topMenu ul li {
  line-height: 50px;
  list-style: none;
  float: left;
  vertical-align: middle;
  text-align: center;
}

.topMenu .menuLink {
  color: #d2d2d2;
  user-select: none;
  text-decoration: none;
  display: block;
  width: 150px;
  font-size: 17px;
  font-family: 'Trebuchet MS', Dotum, Arial;
}

.topMenu .menuLink:hover {
  color: #ffffff;
}

.title {
  color: #ffffff;
  margin: auto;
  max-width: 1000px;
  line-height: 80vh;
  height: 100vh;
}

@keyframes tipsy {
  _0 {
    transform: translateX(-50%) translateY(-50%) rotate(0deg);
  }
  100% {
    transform: translateX(-50%) translateY(-50%) rotate(1440deg);
  }
}

.title div:before,
.title div:after {
  content: '';
  padding: 0.9em 0.4em;
  position: absolute;
  left: 50%;
  width: 40vw;
  height: 36vw;
  top: 50%;
  display: block;
  border: 5px solid rgb(107, 107, 107);
  border-radius: 20%;
  transform: translateX(-50%) translateY(-50%) rotate(0deg);
  animation: 5s infinite ease-in-out tipsy;
}

.title div:before {
  border-color: #ff5c5cd5 #ff5c5cd5 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  z-index: -1;
}

.title div:after {
  border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #ff5c5cd5 #ff5c5cd5;
  /* box-shadow: 25px 25px 25px rgba(46, 46, 49, 0.8); */
}

.title a {
  /* max-width: 500px; */
  user-select: none;
  color: #fffbf1;
  text-shadow: 0 20px 25px #2e2e31, 0 40px 60px #2e2e31;
  font-size: 2.1vw;
  font-weight: bold;
  text-decoration: none;
  letter-spacing: 2px;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.title a:before,
.title a:after {
  content: '';
  padding: 0.9em 0.4em;
  position: absolute;
  left: 50%;
  width: 39vw;
  height: 35vw;
  top: 50%;
  display: block;
  border: 5px solid rgb(107, 107, 107);
  border-radius: 20%;
  transform: translateX(-50%) translateY(-50%) rotate(0deg);
  animation: 25s infinite ease-in-out tipsy;
}

.title a:before {
  border-color: #7a49ff #7a49ff rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  z-index: -1;
}

.title a:after {
  border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #7a49ff #7a49ff;
  /* box-shadow: 25px 25px 25px rgba(46, 46, 49, 0.8); */
}

.wrapper {
  height: auto;
  min-height: 100%;
}

.main {
  margin: auto;
  height: 100%;
  /* max-width: 1000px; */
  /* padding: 1rem; */
}
